:root {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  --fontSizeTitle: 1.5rem;
  --fontSizeHeader: 2rem;
  --slate0: #f8fafc;
  --slate5: #64748b;
  --slate6: #475569;
  --slate7: #334155;
  --complementarySlate5: #ab6731;
  --neutral2: #e5e5e5;
  --neutral5: #737373;
  --zinc2: #f4f4f5;
  --lime5: #84cc16;
  --green5: #22c55e;
  --rose5: #f43f5e;
  --purple5: #a855f7;
  --indigo5: #6366f1;
  --blue5: #3b82f6;
  --outerGap: 1rem;
  --innerGap: 0.5rem;
  --miniGap: 0.3rem;
  --miniPadding: 0.3rem;
  --outerPadding: 0.5rem;
  --innerPadding: 1rem;
  --borders: 1px dotted black;
}

html {
  min-height: 100vh;
}
body {
  min-height: 100%;
}

.App {
  max-width: 100vw;
}

h2,
h3,
h4,
h5,
h6,
p,
body,
fieldset {
  margin: 0;
  padding: 0;
}

h3 {
  font-size: var(--fontSizeTitle);
}

button {
  background-color: inherit;
  border: none;
}

input {
  max-width: 30vw;
}

.mainSection {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: var(--miniGap);
}
.header {
  background-color: var(--slate7);
  font-size: var(--fontSizeTitle);
  color: white;
  padding: var(--outerPadding);
  display: flex;
  flex-direction: column;
}
h2 {
  align-self: center;
}
.sectionTitle {
  align-self: center;
  display: flex;
  gap: var(--miniGap);
}
.personalForm,
.skillForm,
.experienceForm,
.educationForm {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: var(--innerGap);
  padding: var(--outerPadding);
}
.personalForm {
  align-items: center;
  align-self: center;
}
.skillForm {
  flex-direction: row;
  align-items: center;
}
.innerFormDiv {
  display: flex;
  justify-content: space-between;
  align-content: space-between;
  gap: var(--outerGap);
  padding: var(--miniPadding);
  /*   min-width: 45vw; */
}
label {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: var(--miniGap);
}
.btnContainer {
  display: flex;
}

@media screen and (min-width: 1060px) {
  .skillForm {
    flex-direction: row;
  }
  .skillsbtncontainer {
    align-self: flex-end;
  }
}
@media screen and (max-width: 1060px) {
  .skillForm {
    flex-direction: column;
  }
  .skillsbtncontainer {
    align-self: center;
  }
}

.addBtn {
  margin-top: 0.3rem;
  align-self: center;
}

.textfield {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: var(--outerGap);
}

.textAboutMe {
  height: 7vh;
}

.labelAbout {
  display: flex;
  flex-direction: column;
  align-self: stretch;
}

.topEdit {
  margin-top: 2rem;
}

.innerFormUpload {
  display: flex;
  flex-direction: column;
  gap: var(--innerGap);
}

.uploadedImg {
  display: flex;
  gap: var(--outerGap);
}

.deleteImg {
  margin-bottom: 1rem;
}

.goal {
  max-width: 30vw;
}

.textfield {
  padding: var(--innerPadding);
}
.top {
  display: flex;
  flex-direction: column;
}
.personalInputWrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
#ageInpt {
  max-width: 25px;
}

